import React, { ReactNode } from "react";
import styled from "styled-components";

import { colors, spacingMap } from "../styleguide";

type DrawerBodyProps = {
	children?: ReactNode
	className?: string
};

const StyledDrawerBody = styled.div`
	position: relative;
	display: flex;
	border-radius: 16px;
	flex-direction: column;
	background: ${colors.white};
	margin: 0px ${spacingMap.xxs} ${spacingMap.xxs} ${spacingMap.xxs};
	box-shadow: 0px 0px 1px 0px rgba(25, 25, 25, 0.32), 0px 2px 8px -2px rgba(25, 25, 25, 0.10);
	flex: 1;
`;

const DrawerBody = ({
	children,
	className,
}: DrawerBodyProps) => (
	<StyledDrawerBody className={className}>
		{children}
	</StyledDrawerBody>
);

export default DrawerBody;
